import Link from "next/link";
import BaseLayout from '../components/Base-Layout';
import Footer from "../components/Footer";

export default function Index() {
    return (
        <div>
            <BaseLayout>

                <div className="container-logos">
                    <div className="fundamental">
                        <Link href='https://fundamental.luzdosaber.virtual.ufc.br' passHref>
                            <a target="_blank">
                                <img className="sun-logo" src="static/logo-raios.png" alt="Logo Sol Luz do Saber" />
                                <img className="sun-logo" src="static/logo-rosto-0.png" alt="Logo Sol Luz do Saber" />
                            </a>
                        </Link>
                    </div>
                    <div className="eja">
                        <Link href='https://eja.luzdosaber.virtual.ufc.br' passHref>
                            <a target="_blank">
                                <img className="logo-eja" src="static/logo_eja.png" alt="Logo Sol Luz do Saber" />
                            </a>
                        </Link>
                    </div>
                    
                </div>

            </BaseLayout>

            <style jsx>{`
                .container-logos a {
                    display: block;
                    position: relative;
                }
                .container-logos {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    height: 400px;
                    margin-top: 15%;
                }
                .fundamental {
                    border: 1px solid black;
                    // display: flex;
                    // align-items: center;
                    // justify-content: center;
                    position: relative;
                    width: 25em;
                    height: 15em;
                }
                .sun-logo {
                    position: absolute;
                    width: 400px;
                    max-width: 100%;
                }
                .eja {
                    position: relative;
                    top: 25px;
                    width: 30em;
                    height: 20em;
                }
                .logo-eja {
                    position: absolute;
                    width: 400px;
                    max-width: 100%;
                }
            `}</style>
        </div>
    );
}